body {
  font-family: "Hind Guntur", sans-serif;
  height: 100%;
  width: 100%;
  background: #000;
  margin: 0%;
  padding: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
}
h2 {
  color: aliceblue;
  border-style: solid;
  border-width: 5px;
  border-color: azure;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 30px;
  padding-left: 30px;
}

.btn {
  border: 26 px solid white;
  margin-top: 50px;
  font-family: monospace;
  border-radius: 15px;
  font-size: 39px;
  margin-left: 65px;
}

.btn:hover {
  border: 26 px solid rgb(0, 0, 0);
  margin-top: 50px;
  font-family: monospace;
  border-radius: 15px;
  font-size: 39px;
  margin-left: 47px;
  color: rgb(255, 255, 255);
  background-color: black;
}

@keyframes bg {
  0% {
    background: #000;
  }
  100% {
    background: #fff;
  }
}
h1 {
  font-size: 64px;
  transition: 0.5s;
}
.loading {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.loading div {
  position: absolute;
  background: #fff;
  opacity: 1;
  border-radius: 50%;
  animation: loading 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.loading div:nth-child(2) {
  animation-delay: -0.7s;
}
@keyframes loading {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}

.fa-instagram {
  position: absolute;
  color: #000;
  top: 3%;
  right: 2%;
  font-size: 38px;
}
.fa-instagram:hover {
  font-size: 42px;
  color: #981919;
  transition: all 0.1s linear;
  cursor: pointer;
}

.App {
  text-align: center;
  justify-content: center;
  align-items: center;
}

.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgb(245, 244, 244);
  transition: 0.3s;
  margin-top: 30px;
  margin-left: 95px;
  margin-top: 90px;
  margin-bottom: 30px;
  height: 320px;
  width: 282px;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  border: 0.5px solid snow;
}

/* Add some padding inside the card container */
.container {
  padding: 20px;
  color: azure;
}

